.ng2-c-backdrop {
  background: rgba(0, 0, 0, 0.5);
  bottom: 0;
  height: 100%;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  visibility: visible;
  width: 100%; }

.ng2-c-backdrop--is-transparent {
  background: transparent; }

.ng2-c-backdrop--is-blurred {
  background-image: linear-gradient(to right, #28648a, #004160);
  background-repeat: no-repeat;
  background-size: cover;
  animation: backdrop-fade-in 150ms ease-out; }

.body--is-blurred .ng2-c-backdrop {
  background-image: linear-gradient(to right, #28648a, #004160);
  background-repeat: no-repeat;
  background-size: cover;
  animation: backdrop-fade-in 150ms ease-out; }

@keyframes backdrop-fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
